<template>
<div class="page page-current">
    <header class="bar bar-nav">
        <h1 class="title"></h1>
    </header>
    <div class="bar bar-header-secondary-home">
        <router-link to="/home/create">
            <span class="icon icon-home"></span>
            <span class="tab-label"></span>
        </router-link>
        <router-link to="/home/friend">
            <span class="icon icon-home"></span>
            <span class="tab-label"></span>
        </router-link>
    </div>
    <!-- <div class="topTips warning"><i class="icon-warning"></i></div> -->
    <bottomBar></bottomBar>
    <div class="content">
        <!--  -->
        <div class="description">
            <br> 1
            <br> 21 5
            <br> 3
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <ul class="tmpl-list clearfix" v-if="templateTotal>0">
                    <li class="item active">
                        <div class="inner">
                            <div class="img-wrap">
                                <img src="/static/demo/demo.jpg" alt="">
                            </div>
                            <div class="name">Menthonlatum  50</div>
                            <div class="ctrl">
                                <span class="money"><i></i>398</span>
                                <a href="javascipt:;"><i class="icon-more"></i></a>
                            </div>
                            <span class="checkbox is-checked"></span>
                        </div>
                        <div class="mask">
                            <a href="javascipt:;" class="edit-btn"></a>
                            <a href="javascipt:;" class="del-btn"></a>
                        </div>
                    </li>
                    <li class="item">
                        <div class="inner">
                            <div class="img-wrap">
                                <img src="/static/demo/demo.jpg" alt="">
                            </div>
                            <div class="name">Menthonlatum  50</div>
                            <div class="ctrl">
                                <span class="money"><i></i>398</span>
                                <a href="javascipt:;"><i class="icon-more"></i></a>
                            </div>
                            <span class="checkbox"></span>
                        </div>
                        <div class="mask">
                            <a href="javascipt:;" class="edit-btn"></a>
                            <a href="javascipt:;" class="del-btn"></a>
                        </div>
                    </li>
                    <li class="item">
                        <div class="inner">
                            <div class="img-wrap">
                                <img src="/static/demo/demo.jpg" alt="">
                            </div>
                            <div class="name">Menthonlatum  50</div>
                            <div class="ctrl">
                                <span class="money"><i></i>398</span>
                                <a href="javascipt:;"><i class="icon-more"></i></a>
                            </div>
                            <span class="checkbox is-checked"></span>
                        </div>
                        <div class="mask">
                            <a href="javascipt:;" class="edit-btn"></a>
                            <a href="javascipt:;" class="del-btn"></a>
                        </div>
                    </li>
                </ul>
                <div class="no-tmpl" v-else>
                    <router-link to="/home/create">
                        <span class="icon"><i class="icon-addtmpl"></i></span>
                        <span class="text"></span>
                    </router-link>
                </div>
            </div>
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <div class="list-block">
                    <ul>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-radio-group v-model="radio2">
                                            <el-radio :label="3"></el-radio>
                                            <el-radio :label="6"></el-radio>
                                            <el-radio :label="9"></el-radio>
                                        </el-radio-group>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <div class="list-block">
                    <ul>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="province-wrap">
                    <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                        <div class="clearfix">
                            <template v-for="(city,index) in cities" v-if="index<=21">
                                    <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
                                </template>
                        </div>
                        <div class="clearfix half">
                            <template v-for="(city,index) in cities" v-if="index>21&&index<=25">
                                    <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
                                </template>
                        </div>
                        <div class="clearfix all">
                            <template v-for="(city,index) in cities" v-if="index===26">
                                    <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
                                </template>
                        </div>
                        <div class="clearfix">
                            <template v-for="(city,index) in cities" v-if="index>26&&index<=30">
                                    <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
                                </template>
                        </div>
                        <div class="clearfix">
                            <template v-for="(city,index) in cities" v-if="index>30">
                                    <el-checkbox :label="city" :key="city">{{city}}</el-checkbox>
                                </template>
                        </div>
                    </el-checkbox-group>
                </div>
            </div>
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <div class="list-block">
                    <ul>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <input type="text" placeholder="">
                                    </div>
                                </div>
                            </div>
                        </li>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-title label"></div>
                                    <div class="item-input">
                                        <input type="text" placeholder="">
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="main-box">
            <div class="box-hd">
                <span class="text"></span></div>
            <div class="box-bd">
                <div class="list-block">
                    <ul>
                        <li>
                            <div class="item-content">
                                <div class="item-inner">
                                    <div class="item-input">
                                        <textarea placeholder=""></textarea>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import bottomBar from '@/components/bottomBar';
// 
const cityOptions = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];

export default {
    name: 'homeBuyer',
    data() {
        return {
            checkAll: false,
            checkedCities: [],
            cities: cityOptions,
            isIndeterminate: true,
            radio2: 3,
            templateData: [],
            templateTotal: 0
        };
    },
    created() {
        this.getTemplateFun();
    },
    methods: {
        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        handleCheckAllChange(val) {
            this.checkedCities = val ? cityOptions : [];
            this.isIndeterminate = false;
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        handleCheckedCitiesChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.cities.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
        },

        /**
         * @Author      weiberZeng
         * @DateTime    2018-06-11
         * @lastTime    2018-06-11
         * @description 
         */
        getTemplateFun() {
            this.$http.get('/tasktemplate/list', {
                params: {
                    pageIndex: 1,
                    pageSize: 10
                }
            }).then((response) => {
                if (response.data.success) {
                    this.templateData = response.data.data.list;
                    this.templateTotal = response.data.data.total;
                }
            });
        }
    },
    components: {
        bottomBar
    }
};
</script>



